import React from 'react';
import Container from '@material-ui/core/Container';
import DatePickers from '../Components/DateTimePicker';
import ContainedButtons from '../Components/Button1';
import BasicButtonGroup from '../Components/ButtonGroup';
import Checkboxes from '../Components/Checkbox';
import FloatingActionButtons from '../Components/FloatingActionButton';
import MaterialUIPickers from '../Components/DatePicker';
import SimpleBottomNavigation from '../Components/BottomNav';
import SimpleBreadcrumbs from '../Components/BreadCrumb';
import CustomizedRadios from '../Components/RadioButton';
import ControlledAccordions from '../Components/ControlledAccordion';

export default function Hotel() {
  return (
    <React.Fragment>
        <Container fixed>
        <SimpleBottomNavigation/>
          <DatePickers/>
          <ContainedButtons />
         <BasicButtonGroup/>
         <Checkboxes/>
         <FloatingActionButtons />
         <MaterialUIPickers/>
         <SimpleBreadcrumbs/>
         <CustomizedRadios/>
         <ControlledAccordions/>
        </Container>
    </React.Fragment>
  );
}